import React, { Component } from "react";
import { dati } from '../assets/dati';
import Grafico from "./Grafico";
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import Grid from '@material-ui/core/Grid';

let giorni = () => {
    let gg = [];
    dati.forEach(element => {
        if (!gg.includes(element.DateRep))
            gg.push(element.DateRep)
    });
    return gg;
}



let recuperaDatasetPaese = (paese, tipo) => {

    let l = [];
    dati.forEach(element => {
        if (element.CountryExp === paese) {
            l.push(element[tipo]);
        }
    });

    let l_borderColor = "";
    let l_backGroundColor = "";

    switch (paese) {
        case "Italy":
            l_borderColor = '#8000ff';
            l_backGroundColor="#d9b3ff";
            break;

        case "Spain":
            l_borderColor = '#e68a00';
            l_backGroundColor="#ffe0b3";
            break;

        case "France":
            l_borderColor = '#3399ff';
            l_backGroundColor="rgba(75,192,192,0.4)";
            break;

        case "Germany":
            l_borderColor = '#00b33c';
            l_backGroundColor="#ccffdd";
            break;

        case "China":
            l_borderColor = '#ff0066';
            l_backGroundColor="#ffb3d1";
            break;

        default:
            l_backGroundColor="#d9b3ff";
            l_borderColor = 'black';

    }


    return {
        label: paese,
        data: l,
        fill: true,

        lineTension: 0.3,
        backgroundColor: l_backGroundColor,
        borderColor: l_borderColor,
        borderCapStyle: 'butt',
        borderDash: [],
        borderDashOffset: 0.0,
        borderJoinStyle: 'miter',
        pointBorderColor: 'black',
        pointBackgroundColor: 'white',
        pointBorderWidth: 3,
        pointHoverRadius: 4,
        pointHoverBackgroundColor: 'red',
        pointHoverBorderColor: 'red',
        pointHoverBorderWidth: 5,
        pointRadius: 1,
        pointHitRadius: 10,

    }

}



const graficoVuoto = {
    labels: giorni(),
    datasets: [
    ]
};





class Main extends Component {


    constructor(props) {
        super(props);
        this.state = {
            grafico: graficoVuoto,
            tipo: "NewDeaths",
            showItaly: false,
            showChina: false,
            showFrance: false,
            showGermany: false,
            showSpain: false,

        }
    }


    componentWillMount() {

    }
    componentDidMount() {
    }







    ////////////////////////////////////////////////////////////////





    handleCheck = name => event => {
        this.setState({
            [name]: event.target.checked
        });


        let nomePaese = name.substring(4);
        let daAggiungere = event.target.checked;


        let ds = recuperaDatasetPaese(nomePaese, this.state.tipo);
        console.log(ds);

        this.setState((prevState) => {

            let newState = prevState;

            if (daAggiungere) {
                let esiste =
                    newState.grafico.datasets.some((elem) => {
                        return elem.label === nomePaese
                    })

                if (!esiste)
                    newState.grafico.datasets.push(ds)
            } else {
                newState.grafico.datasets.forEach((elem, index, object) => {
                    if (elem.label === nomePaese) {
                        object.splice(index, 1);
                    }
                })

            }

            return newState;
        }
        );


    };

    handleChange = name => event => {
        this.setState({
            [name]: event.target.value
        });

    };


    handleChangeTipo = name => event => {

        let tipo = event.target.value;
        console.log(tipo);

        this.setState((prevState) => {

            let newState = prevState;

            newState.tipo = tipo;
            newState.grafico.datasets = []

            newState.showChina = false;
            newState.showItaly = false;
            newState.showFrance = false;
            newState.showGermany = false;
            newState.showSpain = false;

            return newState;
        }
        );






    };



    renderCheck = nome => {
        let nomeCheck = "show" + nome;
        let valoreStato = this.state[nomeCheck];

        return (

            <FormControlLabel
                control={
                    <Switch checked={valoreStato}
                        onChange={this.handleCheck(nomeCheck)} value={nomeCheck}
                        color="primary"
                    />
                }
                label={nome}
            />


        );
    };

    ////////////////////////////////////





    render() {

        // NewDeaths -- NewConfCases

        return (
            <Grid
                container
                direction="column"
                justify="space-evenly"
                alignItems="stretch"
            >
                <h1>Situazione contagi / decessi per Coronavirus al 14/03/2020</h1>

                <fieldset>
                    <legend>Opzioni</legend>

                    <FormGroup row>
                        {this.renderCheck("Italy")}
                        {this.renderCheck("France")}
                        {this.renderCheck("China")}
                        {this.renderCheck("Germany")}
                        {this.renderCheck("Spain")}
                    </FormGroup>

                <RadioGroup row value={this.state.tipo} name="tipo" onChange={this.handleChangeTipo("tipo")}>
                    <FormControlLabel value="NewDeaths" control={<Radio />} label="Nuovi decessi" />
                    <FormControlLabel value="NewConfCases" control={<Radio />} label="Nuovi contagi" />
                </RadioGroup>
                </fieldset>

                <Grafico data={this.state.grafico}></Grafico>

                <p>I dati sono stati resi disponibili da <a href="https://www.ecdc.europa.eu/en/publications-data/download-todays-data-geographic-distribution-covid-19-cases-worldwide">European Centre for Disease Prevention and Control</a></p>
                <p> Versione 0.1 da F. Vuoto</p>
            </Grid>
        );
    }
}

export default Main;

